header{
    height: 10vh;
    line-height: 10vh;
    padding: 0 25px;
}
header .container{
    padding-top: 15px;
}
header ul li{
    margin-left: 30px;
}
header ul li a{
    transition: 0.5s;
    font-size: 13px;
    font-weight: 500;
    letter-spacing: 1px;
}
header ul li a:hover{
    color: #ff014f;
}
.home-btn{
    padding: 18px 25px;
    border-radius: 6px;
    transition: 0.3s all ease;
    cursor: pointer;
    height: fit-content;
    color: #ff014f;
}
.home-btn{
    background: linear-gradient(145deg, #e2e8ec, #ffffff);
    box-shadow: 4px 4px 8px #cbcbcb, 4px -4px 8px #ffffff;
}
.home-btn:hover{
    background-color: #ff014f;
    color: #ffffff;
}
.open{
    display: none;
}
.header.active{
    height: 15vh;
    position: fixed;
    top: 0;
    z-index: 1000;
    width: 100%;
    background-color: #ecf0f3;
    box-shadow: -1px 11px 24px rgba(0, 0, 0, 0.75);
    transition: 0.5s; 
}
@media (max-width: 768px) {
    header ul {
        padding-top: 30px;
    }
    .nav-links-mobile{
        width: 60%;
        height: 100vh;
        background-color: #ffffff;
        z-index: 888;
        position: absolute;
        display: block;
        list-style: none;
        box-shadow: rgba(50, 50, 93, 0.23) 0 50px 100px -20px, rgba(0, 0, 0, 0.3) 0 30px 60px -30px;
        top: 0;
        left: 0;
        transition: all 0.5s ease-in-out;
    }
    header ul li{
        width: 100%;
        transition: 0.5s;
        text-transform: uppercase;
        line-height: 70px;
    }
    header .link{
        display: none;
    }
    .close, .open{
        position: absolute;
        top: 35px;
        right: 20px;
        color: #ff014f;
        display: block;
        font-style: 25px;
        z-index: 999;
    }
    .close{
        right: 45%;
    }
    .close.home-btn{
        color: #ff014f;
        border-radius: 50%;
        padding: 0;
        width: 60px;
        height: 60px;
        line-height: 60px;
    }
    .header.active{
        height: 10vh;
    }
}
.demo{
    height: 200vh;
}