.hero {
    position: relative;
    margin-top: 80px;
    padding: 0 25px;
}

.hero h3 {
    font-weight: 300;
    letter-spacing: 2px;
}

.hero h1 {
    font-size: 60px;
    font-weight: bold;
}

.hero h1 span {
    color: #ff014f;
}

.hero h2 {
    font-size: 50px;
    font-weight: bold;
    color: #ff014f;
}

.hero h2 span {
    color: #000;
}

.hero p {
    color: #878e99;
    font-size: 18px;
    line-height: 30px;
    margin-top: 30px;
}

.hero_btn {
    margin-top: 20px;
}

.hero_btn h4 {
    font-weight: 500;
    letter-spacing: 2px;
    color: #43474b;
    font-size: 15px;
    margin: 30px 0 30px 0;
}

.hero_btn button {
    margin-right: 30px;
}

.hero_btn i {
    font-size: 20px;
}

.hero_btn button img {
    width: 20px;
    height: 20px;
}

.hero .left {
    margin-right: 10%;
}

.hero .right {
    width: 40%;
    position: relative;
}

.hero .right_img {
    background: linear-gradient(145deg, #e2e8ec, #ffffff);
    box-shadow: 4px 4px 8px #cbcbcb, 4px -4px 8px #ffffff;
    border-radius: 10px;
    width: 510px;
    height: 450px;
    margin-top: 20%;
}

.hero .right_img img {
    position: absolute;
    top: 0;
    right: 10%;
    width: 510px;
}

@media (max-width: 768px) {
    .hero .right_img img {
        position: absolute;
        top: 0;
        right: 10%;
        width: 92%;
        height: 95%;
    }

    .hero .right,
    .hero .left {
        width: 100%;
        margin-left: 20px;
        margin-bottom: 30px;
    }

    .hero .f_flex {
        flex-direction: column;
        flex-direction: column-reverse;
    }

    .hero .right_img {
        width: 95%;
        height: 54vh;
    }
}